.NavBar{
    margin-top: 2%;
}
.selected {
    font-Weight: "bold";
    color: "red";
}
.bannerContainer{
    background-color: aquamarine;
    height: 100px;
    width: 500px;
}

.logoContainer{
    height: 100px;
    width: 100px;
}

.questionContainer{
    margin-top: 20px;
    border-bottom: 1px solid #c8ced3;
    padding: 10px;
}
.video-editor-lab .video-wrapper{ text-align: center;}
.video-editor-lab .video-wrapper video{border-radius:4px;}
.video-editor-lab .row  [class*="col-"]{margin-bottom:1rem;}
.video-editor-lab .form-field label{font-size:16px; font-weight: 500;}
.video-editor-lab .form-field{background: #f7f7f7;padding: 1rem;border-radius: 3px;min-height: 100%;    }
.video-editor-lab .form-field .custom-file{position: relative;display: inline-block;width: 100%;height: 40px;margin-bottom: 0;background: #39b2d5;color: #fff;padding: 7px 10px 0 10px;border-radius: 2px; margin-bottom:1rem;}
.video-editor-lab .form-field img{max-width: 100%;}
.video-editor-lab .video-logo-upload  .preview-image{width: 100px;height: 100px;border-radius: 100%;object-fit: contain;object-position: center;border: 3px solid #eaeaea;background: #fbfbfb;}
.video-editor-lab .banner-upload .preview-image{border: 3px solid #eaeaea;background: #fbfbfb;    height: 100px;object-fit: contain;}
.upload-file-btn{width: 100%;border: 2px dashed #fafafa;height: 70px;background: #39b2d5;padding: 19px 44px;color: #fff;}
.video-wrapper{position: relative;max-width: 900px;margin: 2rem auto 0;}
.video-wrapper [class*="base-logo"]{position: absolute; top:10px;}
.video-wrapper [class*="base-logo"].base-logo{left:10px;}
.video-wrapper [class*="base-logo"].c-base-logo{right:10px;}
.video-wrapper [class*="base-logo"]> img{width:60px; height:60px; border-radius:100%; object-fit: contain; object-position:center; opacity: 0.6; cursor: pointer;}
.video-wrapper [class*="base-logo"]> img:hover{opacity: 1;}
.video-wrapper .qa-content{    position: absolute;bottom: 0;width: 100%;background: rgba(0,0,0,0.7); left:0px; color:#fff;}
.video-wrapper .qa-content .qa-item-list{padding:1rem;}
.video-wrapper .qa-content .qa-item-list h4.title{font-size: 26px; margin-bottom: 1.5rem;}
.video-wrapper .qa-content .qa-item-list .qa-options input{position: absolute; opacity: 0; visibility: hidden;}
.video-wrapper .qa-content .qa-item-list .qa-options{display: flex; display: -webkit-flex; justify-content: space-between;}
.video-wrapper .qa-content .qa-item-list .qa-options label{margin-bottom: 0px;  flex:0 0 30%; cursor: pointer; max-width:24%;}
.video-wrapper .qa-content .qa-item-list .qa-options label span{ background-color: #fff; color:#000; font-size: 14px; display:block;padding:10px; font-size: 14px; }



