============================================*/

.advert-video-wrapper,
.advert-video-group {
    clear: both;
    position: relative;
    width: 100%;
}

.advert-video-group {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    margin-right: -15px;
    margin-left: -15px;
}
.advert-video-group .advert-video-box {
    width: 100%;
    padding: 15px;
}
.video-card {
    position: relative;
    width: 100%;
    padding: 25px 30px;
    background: #ffffff;
    border: 1px solid rgba(0,0,0,0.20);
    border-radius: 7px;
    -webkit-box-shadow: 0 5px 15px -7px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 5px 15px -7px rgba(0,0,0,0.1);
    box-shadow: 0 5px 15px -9px rgba(0,0,0,0.1);

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.video-card .video-image-group{
    width: 200px;
}

.video-card .video-image-group img{
    width: 100%;
}
.video-card .video-details-group {
    width: calc(100% - 250px);   
}
.video-card .video-details-group .group-title {
    font-size: 18px;
    font-weight: 600;
    line-height: 1.2;
    margin-bottom: 5px;
    color: #000;
}
.video-card .video-details-group .group-details {
    font-size: 14px;
    line-height: 1.5;
    font-weight: 400;
    color: #999999;
    margin-bottom: 0;
}
.video-card .video-details-group .group-details strong{
    font-weight: 600;
    color: #333333;   
}
.video-card .video-details-group .group-details span{
    padding: 0 15px 0 7px;
}
.video-card .video-details-group .group-filds {
    margin-top: 10px;
}